.charaHeader {
    display: inline;
}

.brighteName, .lalehName, .lydiName,
.nakoaName, .rinName, .robynName, 
.suminaName {
    font-size: 210%;
    font-family: 'Playfair Display', serif;
    display: inline-block;
}

.brighteName {
    color: #514a94;
}

.lalehName {
    color: #a57a38;
}

.lydiName {
    color: #92c6e0;
}

.nakoaName {
    color: #b55038;
}

.rinName {
    color: #3f6a3a;
}

.robynName {
    color: #5b7086;
}

.suminaName {
    color: #b81828;
}

.titleInfo {
    display: inline-block;
    flex-direction: column;
    text-align: left;
    margin-left: 10px;
    line-height: 0.2;  
}

.pronounciation {
    color: #939494;
}

.tagline {
    font-weight: bold;
}

.fullbodyImg {
    width: 175px;
    height: 255px;

    margin: 0px 5px -10px 5px;
}

.summary {
    display: inline-block; 
    text-align: left; 
    margin-left: 5px;
}

.summAtt {
    margin-top: -10px;
}

.brighteAttribute, .lalehAttribute,
.lydiAttribute, .nakoaAttribute,
.rinAttribute, .robynAttribute,
.suminaAttribute {
    font-weight: bold;
    display: inline-block;
    padding: 0px 3px 0px 3px;
    margin-bottom: -10px;
} 

.brighteAttribute {
    background-color: rgba(81, 74, 148, 0.4);
}

.lalehAttribute {
    background-color: rgb(165, 122, 56, 0.4);
}

.lydiAttribute {
    background-color: rgb(146, 198, 224, 0.4);
}

.nakoaAttribute {
    background-color: rgb(181, 80, 56, 0.4);
}

.rinAttribute {
    background-color: rgb(63, 106, 58, 0.4);
}

.robynAttribute {
    background-color: rgb(91, 112, 134, 0.4);
}

.suminaAttribute {
    background-color: rgba(184, 24, 40, 0.4);
}

.value {
    display: inline-block;
    margin-left: 10px;
    margin-bottom: -10px;
    color: #636363;
}

