.card, 
.brighteCard, .brighteCardIcon, 
.lalehCard, .lalehCardIcon,
.lydiCard, .lydiCardIcon,
.nakoaCard, .nakoaCardIcon,
.rinCard, .rinCardIcon,
.robynCard, .robynCardIcon,
.suminaCard, .suminaCardIcon {
    width: 153.125px;
    height: 216.5625px;
    margin: 10px;
    border: solid black 1px;
    border-radius: 5px;
    background: url(/kingpin/static/media/card_temp.47749acb.png) no-repeat;
    background-size: 153.125px 216.5625px;
    transition: scaleX(-1);
}

/*BRIGHTE -----------------------------------------------*/
.brighteCard {
    background: url(/kingpin/static/media/brighte_card.398b5baf.png) no-repeat;
    background-size: 153.125px 216.5625px;
}

.brighteCard:hover {
    background: url(/kingpin/static/media/brighte_icon.777cf076.png) no-repeat;
    background-size: 153.125px 216.5625px;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    cursor: pointer;
}

.brighteCardIcon {
    background: url(/kingpin/static/media/brighte_icon.777cf076.png) no-repeat;
    background-size: 153.125px 216.5625px;
