.kingpin_body {
    width: 100%;
    height: 100%;
    display: flex;
  
    font-size: 120%;
    font-family: Roboto, sans-serif;
}

.kingpin_details {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

.kingpin_title {
    font-size: 350%;
    font-family: 'Playfair Display', serif;
}

.kingpin_excerpt {
    padding-left: 20%;
    padding-right: 20%;
    text-align: center;
}

.kingpin_selection {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: center;
}

.arrow, .upArrow {
    display: flex;

    width: 75px;
    height: 5%;

    margin-left: 47.5%;
    margin-bottom: 5%;
}

.upArrow {
    transform: rotate(180deg);
    cursor: pointer;
}

.charaNames, .brighteActive, .lalehActive, 
.lydiActive, .nakoaActive, .rinActive,
.robynActive, .suminaActive {
    margin-top: 230px;
}

.card, 
.brighteCard, .brighteCardIcon, 
.lalehCard, .lalehCardIcon,
.lydiCard, .lydiCardIcon,
.nakoaCard, .nakoaCardIcon,
.rinCard, .rinCardIcon,
.robynCard, .robynCardIcon,
.suminaCard, .suminaCardIcon {
    width: 153.125px;
    height: 216.5625px;
    margin: 10px;
    border: solid black 1px;
    border-radius: 5px;
    background: url('../img/card_temp.png') no-repeat;
    background-size: 153.125px 216.5625px;
    -moz-transition: scaleX(-1);
    -webkit-transition: scaleX(-1);
    -o-transition: scaleX(-1);
    -ms-transition: scaleX(-1);
    transition: scaleX(-1);
}

/*BRIGHTE -----------------------------------------------*/
.brighteCard {
    background: url('../img/brighte_card.png') no-repeat;
    background-size: 153.125px 216.5625px;
}

.brighteCard:hover {
    background: url('../img/brighte_icon.png') no-repeat;
    background-size: 153.125px 216.5625px;
    -moz-transform: scaleX(1);
    -webkit-transform: scaleX(1);
    -o-transform: scaleX(1);
    -ms-transform: scaleX(1);
    transform: scaleX(1);
    cursor: pointer;
}

.brighteCardIcon {
    background: url('../img/brighte_icon.png') no-repeat;
    background-size: 153.125px 216.5625px;
    border: solid #514a94 3px;
}

.brighteActive {
    color: #514a94;
    font-weight: bold;
}

